.productlist-item-image {
  width: 140px;
  height: 140px;
  margin-bottom: 8px;
  padding-top: 4px;
}

.productlist-item-price {
  font-size: 16px;
  margin-bottom: 8px;
  font-weight: bold;
  color: #000000;
}

.productlist-item-name {
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 8px;
  font-size: 14px;
  color: #171e27;
  margin-bottom: 4px;
}

.productlist-item-container {
  position: relative;
}

.productlist-item-subtitle {
  font-size: 14px;
  color: #9b9b9b;
}

.productlist-buy-button {
  display: none;
  position: absolute;
  top: 25%;
  left: 25%;
  width: 50%;
  padding: 0.2em 0;
  background-color: #530000;
  border-radius: 4px;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

.productlist-item:hover + .productlist-buy-button {
  display: block;
}

.productlist-buy-button:hover {
  display: block;
  background-color: #630000;
}



